<template>
  <div>
      <el-container>

          <!-- 添加节点工具栏 -->
          
          <el-drawer
            :visible.sync="isAdding"
            :with-header="false">
                <div class="nodeCreator">
                    <el-form label-position="right" label-width="80px">
                        <el-form-item label="节点名称">
                            <el-input v-model="newName" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="节点形状">
                            <el-select v-model="newShape" placeholder="请选择">
                                <el-option label="圆形" value="circle"></el-option>
                                <el-option label="矩形" value="rect"></el-option>
                                <el-option label="椭圆形" value="ellipse"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="节点颜色">
                            <el-color-picker v-model="newColor"></el-color-picker>
                        </el-form-item>
                        <el-form-item label="边框颜色">
                            <el-color-picker v-model="newStrokeColor"></el-color-picker>
                        </el-form-item>
                        <el-form-item :label="newp1Name">
                            <el-slider v-model="newP1" :step="1"></el-slider>
                        </el-form-item>
                        <el-form-item :label="newp2Name" v-show="newShape != 'circle'">
                            <el-slider v-model="newP2" :step="1"></el-slider>
                        </el-form-item>
                        <el-form-item label="边框宽度">
                            <el-slider v-model="newStrokeWidth" :format-tooltip="formatTooltip"></el-slider>
                        </el-form-item>
                    </el-form>
                    <el-button type="primary" @click.native="createNode()"> 添加 </el-button>
                </div>
            </el-drawer>
          

          <!-- 侧边栏Editor -->
          <el-aside>
            <div class="Editor">
                <el-form label-position="right" label-width="80px">
                    <el-form-item label="节点名称">
                        <el-input v-model="selectedName" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="节点形状">
                        <el-select v-model="selectedShape" placeholder="请选择">
                            <el-option label="圆形" value="circle"></el-option>
                            <el-option label="矩形" value="rect"></el-option>
                            <el-option label="椭圆形" value="ellipse"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="节点颜色">
                        <el-color-picker v-model="selectedColor"></el-color-picker>
                    </el-form-item>
                    <el-form-item label="边框颜色">
                        <el-color-picker v-model="selectedStrokeColor"></el-color-picker>
                    </el-form-item>
                    <el-form-item :label="p1Name">
                        <el-slider v-model="selectedP1" :step="1"></el-slider>
                    </el-form-item>
                    <el-form-item :label="p2Name" v-show="selectedShape != 'circle'">
                        <el-slider v-model="selectedP2" :step="1"></el-slider>
                    </el-form-item>
                    <el-form-item label="边框宽度">
                        <el-slider v-model="selectedStrokeWidth" :format-tooltip="formatTooltip"></el-slider>
                    </el-form-item>
                </el-form>
            </div>
          </el-aside>
          <el-main>
              <!-- 树形组件 -->
              <div id="tree-container">
                <svg class="overlay">
                    <g>
                        <!-- link集 -->
                        <path v-for="(item,i) in links" :key="'line'+ i" class="link" :d="diagonal(item)"></path>
                        <!-- node 集 -->
                        <g v-for="item in nodes" :key="'node' + item.id" class="node" :transform="nodeTransform(item.x,item.y)" @click="nodeClick(item)">
                            <circle v-if="item.icon.shape == 'circle'"  :r="item.icon.p1" :style="setStyle(item._children,item.icon.color,item.icon.strokeWidth,item.icon.strokeColor)"></circle>
                            <rect v-if="item.icon.shape == 'rect'" 
                                :width="item.icon.p1" :height="item.icon.p2"
                                :style="setStyle(item._children,item.icon.color,item.icon.strokeWidth,item.icon.strokeColor)" 
                                :x="-item.icon.p1/2" :y="-item.icon.p2/2"
                             >
                             </rect>
                            <ellipse v-if="item.icon.shape == 'ellipse'" :rx="item.icon.p1" :ry="item.icon.p2" :style="setStyle(item._children,item.icon.color,item.icon.strokeWidth,item.icon.strokeColor)"></ellipse>
                            <text 
                                :x="item.children || item._children ? -10 : 10"
                                dy=".35em" class="nodeText"
                                :text-anchor="textAnchor(item.children,item._children)"
                                style="fill-opacity: 1;"
                            > 
                                {{item.name}}  
                            </text>
                            <circle class="ghostCircle" r="30" opacity="0.2" pointer-events="mouseover" style="fill: rgb(253, 101, 116);"
                                @mouseover="overCircle(item)"
                                @mouseenter="overCircle(item)"
                                @mouseout="outCircle(item)"
                            >
                            </circle>
                        </g>
                    </g>
                </svg>
            </div>

            <!-- 子节点组件 -->
            <el-row v-for="(list,index) in childrenTable" :key="'list' +  index" style="margin-top:15px; margin-left:10px;">
                <el-col :span="6" v-for="(item, i) in list" :key="'child' + i">
                    <el-card class="childCard" v-if="item.component == 'card' "
                        :body-style="{
                            'height': '80%',
                            'width': '87%',
                            'background-color':'#AEE6CB',
                            'color': '#FFF',
                            'text-align': 'center',
                            'font-weight':'bold',
                            'font-size': '20px',
                         }"
                         @click.native="nodeClick(item)"
                    >
                        <el-button style="float: right;"  type="success" @click.stop="del(item)" icon="el-icon-close" size="mini" plain circle></el-button>
                        <span> {{ item.name }} </span>
                        <el-row v-for="(child,j) in item.children" :key="j" style="text-align: left;font-size:14px;"> {{child.name}} </el-row>
                    </el-card>
                    <el-card class="addCompoent" v-if="item.component == 'add'"
                     :body-style="{
                         'border-style': 'dashed',
                         'height': '70%',
                         'width': '85%',
                         'border-width':'2px',
                         'border-color':'#6C6C76',
                         'border-radius': '4px',
                         'background-color':'#E5E1E0',
                         'font-size':'30px',
                         'text-align': 'center',
                         'position':'relative',
                         'opacity':'0.3'
                         }"
                      shadow="hover"
                    >
                    <div style="position: absolute;top: 50%;left: 50%;transform: translate(-50%, -50%);">
                        <el-button type="info" icon="el-icon-circle-plus" @click.native="isAdding = true;" plain> add </el-button>
                    </div>
                    </el-card>
                </el-col>
            </el-row>
          </el-main>
      </el-container>
  </div>
</template>

<script>
import * as d3 from 'd3v3'
import {event} from 'd3v3'
import testData from '../assets/json/test.json'
import realData from '../assets/json/test2.json'

// Calculate total nodes, max label length
var totalNodes = 0;
var maxLabelLength = 0;
// variables for drag/drop
var draggingNode = null;
var selectedNode = null;
// panning variables
var panSpeed = 200;
var panBoundary = 20; // Within 20px from edges will pan when dragging.
// Misc. variables
var gid = 0;
var duration = 750;
var root;
var pathid = 0;
// size of the diagram
var viewerWidth = 1500;
var viewerHeight = 600;

var tree = null;
var baseSvg = null;
var svgGroup = null;
var dragStarted = false;

var zoomListener = null;

// define a d3 diagonal projection for use by the node paths later on.
var diagonal = d3.svg.diagonal()
    .projection(function(d) {
        return [d.y, d.x];
    });

var subNodes = null;
var domNode = null;
var vim = null;

// A recursive helper function for performing some setup by walking through all nodes
function visit(parent, visitFn, childrenFn) {
    if (!parent) return;

    visitFn(parent);

    var children = childrenFn(parent);
    if (children) {
        var count = children.length;
        for (var i = 0; i < count; i++) {
            visit(children[i], visitFn, childrenFn);
        }
    }
}

// sort the tree according to the node names
function sortTree() {
    tree.sort(function(a, b) {
        return b.name.toLowerCase() < a.name.toLowerCase() ? 1 : -1;
    });
}

// TODO: Pan function, can be better implemented.
function pan(domNode, direction) {
    var speed = panSpeed;
    if (panTimer) {
        clearTimeout(panTimer);
        translateCoords = d3.transform(svgGroup.attr("transform"));
        if (direction == 'left' || direction == 'right') {
            translateX = direction == 'left' ? translateCoords.translate[0] + speed : translateCoords.translate[0] - speed;
            translateY = translateCoords.translate[1];
        } else if (direction == 'up' || direction == 'down') {
            translateX = translateCoords.translate[0];
            translateY = direction == 'up' ? translateCoords.translate[1] + speed : translateCoords.translate[1] - speed;
        }
        scaleX = translateCoords.scale[0];
        scaleY = translateCoords.scale[1];
        let scale = zoomListener.scale();
        svgGroup.transition().attr("transform", "translate(" + translateX + "," + translateY + ")scale(" + scale + ")");
        d3.select(domNode).select('g.node').attr("transform", "translate(" + translateX + "," + translateY + ")");
        zoomListener.scale(zoomListener.scale());
        zoomListener.translate([translateX, translateY]);
        panTimer = setTimeout(function() {
            pan(domNode, speed, direction);
        }, 50);
    }
}

// Define the zoom function for the zoomable tree
function zoom() {
    svgGroup.attr("transform", "translate(" + d3.event.translate + ")scale(" + d3.event.scale + ")");
}

// Function to center node when clicked/dropped so node doesn't get lost when collapsing/moving with large amount of children.
function centerNode(source) {
    let scale = zoomListener.scale();
    let x = -source.y0;
    let y = -source.x0;
    x = x * scale + viewerWidth / 5;
    y = y * scale + viewerHeight / 2;
    d3.select('g').transition()
        .duration(duration)
        .attr("transform", "translate(" + x + "," + y + ")scale(" + scale + ")");
    zoomListener.scale(scale);
    zoomListener.translate([x, y]);
}

// Toggle children function
function toggleChildren(d) {
    if (d.children) {
        d._children = d.children;
        d.children = null;
    } else if (d._children) {
        d.children = d._children;
        d._children = null;
    }
    return d;
}

// Function to update the temporary connector indicating dragging affiliation
var updateTempConnector = function() {
    var data = [];
    if (draggingNode !== null && selectedNode !== null) {
        // have to flip the source coordinates since we did this for the existing connectors on the original tree
        data = [{
            source: {
                x: selectedNode.y0,
                y: selectedNode.x0
            },
            target: {
                x: draggingNode.y0,
                y: draggingNode.x0
            }
        }];
    }
    var link = svgGroup.selectAll(".templink").data(data);

    link.enter().append("path")
        .attr("class", "templink")
        .attr("d", d3.svg.diagonal())
        .attr('pointer-events', 'none');

    link.attr("d", d3.svg.diagonal());

    link.exit().remove();
};

function initiateDrag(d, domNode) {
    draggingNode = d;
    d3.select(domNode).select('.ghostCircle').attr('pointer-events', 'none');
    d3.selectAll('.ghostCircle').attr('class', 'ghostCircle show');
    d3.select(domNode).attr('class', 'node activeDrag');
    d3.selectAll("g.node").sort(function(a, b) { // select the parent and sort the path's
        if (a.id != draggingNode.id) return 1; // a is not the hovered element, send "a" to the back
        else return -1; // a is the hovered element, bring "a" to the front
    });
    // if nodes has children, remove the links and nodes
    if (subNodes.length > 1) {
        // remove link paths
        let links = tree.links(subNodes);
        let nodePaths = d3.selectAll("path.link")
            .data(links, function(d) {
                return d.target.id;
            }).remove();
        // remove child nodes
        let nodesExit = d3.selectAll("g.node")
            .data(subNodes, function(d) {
                return d.id;
            }).filter(function(d, i) {
                if (d.id == draggingNode.id) {
                    return false;
                }
                return true;
            }).remove();
    }
    
    // remove parent link
    //console.log(d3.selectAll('path.link'));
    vm.methods.removeLink();
    /*
    d3.selectAll('path.link').filter(function(d, i) {
        if (d.target.id == draggingNode.id) {
            return true;
        }
        return false;
    }).remove();
    */
    dragStarted = null;
}

function endDrag() {
    selectedNode = null;
    d3.selectAll('.ghostCircle').attr('class', 'ghostCircle');
    d3.select(domNode).attr('class', 'node');
    // now restore the mouseover event or we won't be able to drag a 2nd time
    d3.select(domNode).select('.ghostCircle').attr('pointer-events', '');
    updateTempConnector();
    if (draggingNode !== null) {
        vm.methods.update(root);
        centerNode(draggingNode);
        draggingNode = null;
    }
}
// Helper functions for collapsing and expanding nodes.
function collapse(d) {
    if (d.children) {
        d._children = d.children;
        d._children.forEach(collapse);
        d.children = null;
    }
}

function expand(d) {
    if (d._children) {
        d.children = d._children;
        d.children.forEach(expand);
        d._children = null;
    }
}
let dragListener = d3.behavior.drag()
    .on("dragstart", function(d) {
        if (d == root) {
            return;
        }
        dragStarted = true;
        subNodes = tree.nodes(d);
        d3.event.sourceEvent.stopPropagation();
    })
    .on("drag", function(d) {
        if (d == root) {
            return;
        }
        if (dragStarted) {
            domNode = this;
            initiateDrag(d, domNode);
        }
        var domSvg = document.getElementsByTagName('svg')[0];
        let relCoords = d3.mouse(domSvg);
        if (relCoords[0] < panBoundary) {
            panTimer = true;
            pan(this, 'left');
        } else if (relCoords[0] > (domSvg.getAttribute('width') - panBoundary)) {

            panTimer = true;
            pan(this, 'right');
        } else if (relCoords[1] < panBoundary) {
            panTimer = true;
            pan(this, 'up');
        } else if (relCoords[1] > (domSvg.getAttribute('height') - panBoundary)) {
            panTimer = true;
            pan(this, 'down');
        } else {
            try {
                clearTimeout(panTimer);
            } catch (e) {

            }
        }

        d.x0 += d3.event.dy;
        d.y0 += d3.event.dx;
        var node = d3.select(this);
        node.attr("transform", "translate(" + d.y0 + "," + d.x0 + ")");
        updateTempConnector();
    })
    .on("dragend", function(d) {
        if (d == root) {
            return;
        }
        domNode = this;
        if (selectedNode) {
            // now remove the element from the parent, and insert it into the new elements children
            var index = draggingNode.parent.children.indexOf(draggingNode);
            if (index > -1) {
                draggingNode.parent.children.splice(index, 1);
            }
            if (typeof selectedNode.children !== 'undefined' || typeof selectedNode._children !== 'undefined') {
                if (typeof selectedNode.children !== 'undefined') {
                    selectedNode.children.push(draggingNode);
                } else {
                    selectedNode._children.push(draggingNode);
                }
            } else {
                selectedNode.children = [];
                selectedNode.children.push(draggingNode);
            }
            // Make sure that the node being added to is expanded so user can see added node is correctly moved
            expand(selectedNode);
            sortTree();
            endDrag();
        } else {
            endDrag();
        }
    });

const vm = {
  name: 'dndtree',
  props: {
    msg: String
  },
  data() {
      return {
          isSelect: false,
          isAdding: false,
          //当前选择节点信息
          selectedName: "",
          selectedP1: 0,
          selectedP2: 0,
          selectedShape: "",
          selectedColor: "",
          selectedStrokeWidth: 0,
          selectedStrokeColor: "",
          selectedIndex: 0,
          selectedId: -1,

          //新建节点信息
          newName: "newNode",
          newP1: 6,
          newP2: 6,
          newShape: "circle",
          newColor: "#F2A490",
          newStrokeWidth: 15,
          newStrokeColor: "#E97658",
          newp1Name: "半径",
          newp2Name: "",
          childrenTable: [],
          p1Name: null,
          p2Name: null,
          d3data: null,
          treeData: testData,
          nodes: null,
          links: null,
          array: [1,2],
          diagonal: d3.svg.diagonal().projection(function(d) {return [d.y, d.x];})
      }
  },
  
  mounted() {
    // Get JSON data
    //let treeJSON = d3.json(data , function(error, treeData) {
        var that = this;
        vim = this;
        tree = d3.layout.tree().size([viewerHeight, viewerWidth]);
        // Call visit function to establish maxLabelLength
        visit(that.treeData, function(d) {
            totalNodes++;
            maxLabelLength = Math.max(d.name.length, maxLabelLength);

        }, function(d) {
            return d.children && d.children.length > 0 ? d.children : null;
        });

        // Sort the tree initially incase the JSON isn't in a sorted order.
        sortTree();

        // define the zoomListener which calls the zoom function on the "zoom" event constrained within the scaleExtents
        zoomListener = d3.behavior.zoom().scaleExtent([0.1, 3]).on("zoom", zoom);

        // define the baseSvg, attaching a class for styling and the zoomListener
        baseSvg = d3.select("#tree-container").select("svg")
            .attr("width", viewerWidth)
            .attr("height", viewerHeight)
            .attr("class", "overlay")
            .call(zoomListener);

        // Append a group which holds all nodes and which the zoom Listener can act upon.
        svgGroup = baseSvg.select("g");
        // Define the root
        root = that.treeData;
        root.x0 = viewerHeight / 2;
        root.y0 = 0;

        // Layout the tree initially and center on the root node.
        this.update(root,this);
        centerNode(root);
  //  });
  },
  watch:{
      selectedName(val) {
        this.nodes[this.selectedIndex].name = val;
      },
      selectedColor(val) {
        this.nodes[this.selectedIndex].icon.color = val;
      },
      selectedP1(val) {
        this.nodes[this.selectedIndex].icon.p1 = val;
      },
      selectedP2(val) {
        this.nodes[this.selectedIndex].icon.p2 = val;
      },
      selectedShape(val) {
        this.nodes[this.selectedIndex].icon.shape = val;
        if(this.selectedShape == 'circle') this.p1Name = '半径';
        else if(this.selectedShape == 'rect'){
            this.p1Name = '宽度';
            this.p2Name = '高度';
        }
        else if(this.selectedShape == 'ellipse'){
            this.p1Name = '长轴';
            this.p2Name = '短轴';
        }
      },
      selectedStrokeColor(val) {
        this.nodes[this.selectedIndex].icon.strokeColor = val;
      },
      selectedStrokeWidth(val) {
        this.nodes[this.selectedIndex].icon.strokeWidth = val / 10;
      },

      newShape(val){
        if(this.newShape == 'circle') this.newp1Name = '半径';
        else if(this.newShape == 'rect'){
            this.newp1Name = '宽度';
            this.newp2Name = '高度';
        }
        else if(this.newShape == 'ellipse'){
            this.newp1Name = '长轴';
            this.newp2Name = '短轴';
        }
      }
  },
  methods:{
    //强制更新
    forceUpdate: function(e) {
        this.$forceUpdate();
    },
    // 单击选择节点
    nodeClick: function(d){
        //if (d3.event.defaultPrevented) return; // click suppressed
       // d = toggleChildren(d);
        this.setSelectNode(d);
        this.update(root);
        centerNode(d);
    },
    // 删除节点
    del: function(child){
        // 从父节点的子集中删除该点
        for(let i=0;i<this.nodes[this.selectedIndex].children.length;i++){
            if(this.nodes[this.selectedIndex].children[i].id == child.id){
                this.nodes[this.selectedIndex].children.splice(i,1);
            } 
        }
        //console.log(this.nodes);
        //console.log(this.selectedIndex);
        this.update(root); // update 重新生成nodes集合
        centerNode(this.nodes[this.selectedIndex]);
    },
    // 将子节点放不同行
    cutChildLine:function(children){
        var that = this;
        let list = [];
        if(children && children.length > 0){
            for(let i=0;i<children.length;i++){
                let tmp = children[i];
                tmp.cid = i;
                tmp.component = 'card';
                list.push(tmp);
                if(i%4 == 3){
                    that.childrenTable.push(list);
                    list = [];
                }
            }
        }
        let add = {name:"addCompoent",component:"add"};
        list.push(add);
        if(list.length != 0) that.childrenTable.push(list);
        
    },
    //找到选择节点的index
    findIndex: function(nodeId){
        for(let i=0;i<this.nodes.length;i++){
            if(this.nodes[i].id == nodeId)
                return i;
        }
        return -1;
    },
    //创造新节点
    createNode: function(){
        var that = this;
        // 判断同一目录下是否重名
        if(that.nodes[that.selectedIndex].children != null){
            for(let i=0;i<that.nodes[that.selectedIndex].children.length;i++){
                if(that.nodes[that.selectedIndex].children[i].name == that.newName){
                    that.$notify.error({
                    title: '同名节点已存在',
                    message:'在同一目录下命名同名节点可能会发生错误'
                    });
                    return ;
                }
            }
        }
        let newNode = {
            name: that.newName,
            icon:{
                color: that.newColor,
                shape: that.newShape,
                p1: that.newP1,
                p2: that.newP2,
                strokeWidth: that.newStrokeWidth / 10,
                strokeColor: that.newStrokeColor,
            },
            children:[]
        };
        if(that.nodes[that.selectedIndex].children == null) that.nodes[that.selectedIndex].children = [];
        that.nodes[that.selectedIndex].children.push(newNode);
        that.update(root);
        centerNode(root);
        console.log(that.nodes);
    },
    //设置选择节点
    setSelectNode: function(d){
        var that = this;
        this.selectedName = d.name;
        this.selectedId = d.id;
        this.selectedIndex = this.findIndex(d.id);
        this.selectedP1 = parseInt(d.icon.p1);
        this.selectedP2 = parseInt(d.icon.p2);
        this.selectedShape = d.icon.shape;
        this.selectedColor = d.icon.color;
        this.selectedStrokeWidth = parseInt(d.icon.strokeWidth * 10);
        this.selectedStrokeColor = d.icon.strokeColor;
        this.childrenTable = [];
        this.cutChildLine(d.children);
        if(this.selectedShape == 'circle') this.p1Name = '半径';
        else if(this.selectedShape == 'rect'){
            this.p1Name = '宽度';
            this.p2Name = '高度';
        }
        else if(this.selectedShape == 'ellipse'){
            this.p1Name = '长轴';
            this.p2Name = '短轴';
        }
    },
    //滑块调节小数
    formatTooltip(val) {
        return val / 10;
    },
    //更新点集和边集
    update: function(source) {
        // Compute the new height, function counts total children of root node and sets tree height accordingly.
        // This prevents the layout looking squashed when new nodes are made visible or looking sparse when nodes are removed
        // This makes the layout more consistent.
        var that = vim;
        var levelWidth = [1];
        var childCount = function(level, n) {

            if (n.children && n.children.length > 0) {
                if (levelWidth.length <= level + 1) levelWidth.push(0);

                levelWidth[level + 1] += n.children.length;
                n.children.forEach(function(d) {
                    childCount(level + 1, d);
                });
            }
        };
        childCount(0, root);
        var newHeight = d3.max(levelWidth) * 100; // 100 pixels per line  
        tree = tree.size([newHeight, viewerWidth]);
        sortTree();
        // Compute the new tree layout.
        that.nodes = tree.nodes(root).reverse();
        that.links = tree.links(that.nodes);

        // Set widths between levels based on maxLabelLength.
        that.nodes.forEach(function(d) {
            d.y = (d.depth * (maxLabelLength * 10)); //maxLabelLength * 10px
            // alternatively to keep a fixed scale one can set a fixed depth per level
            // Normalize for fixed-depth by commenting out below line
            // d.y = (d.depth * 500); //500px per level.
        });
        that.nodes.forEach(function(d,i) {
            if(d.id == null) d.id = ++gid;
            d.x0 = d.x;
            d.y0 = d.y;
        });

        //console.log(that.nodes);
        //console.log(that.links);
        
        if(!that.isSelect){
            that.setSelectNode(root);
            that.isSelect = true;
        } 

        //每次 nodes 集更新后 index 变动
        that.selectedIndex = that.findIndex(that.selectedId);
        
        // 每次子集展示的更新
        that.childrenTable = [];
        that.cutChildLine(that.nodes[that.selectedIndex].children);
        // Define the drag listeners for drag/drop behaviour of nodes.
        d3.selectAll("g.node").data(that.nodes);
        //.call(dragListener);
        d3.selectAll("path.link").data(that.links);

        //that.links.forEach(function(d){ if(d.pid == null) d.pid = ++pathid; });
    },
    //拖拽时在连接区域内
    overCircle: function(d){
        selectedNode = d;
        updateTempConnector();
    },
    //拖拽出连接区域内
    outCircle: function(d) {
        selectedNode = null;
        updateTempConnector();
    },
    // node 在图形上的位置
    nodeTransform: function(curNodeX,curNodeY) {
        return `translate(${curNodeY}, ${curNodeX})`
    },
    // 计算图形的style
    setStyle: function(flag, curColor, strokeWidth, strokeColor) {
        let str = "";
        if(flag) str = "fill: lightsteelblue;";
        else str = `fill: ${curColor};`;
        return str + `stroke-width:${strokeWidth}; stroke: ${strokeColor};`;
    },
    // 计算文字排列方式
    textAnchor: function(bool1,bool2) {
        return bool1 || bool2 ? "end" : "start";
    },
  }
};   

export default vm;

</script>

